/* ---------------------- timer */

.timer {
  text-align: center;
  font-family: Arial, sans-serif;
}

.clockface {
  font-size: 2em;
  margin-bottom: 10px;
}

.actions {
  display: flex;
  justify-content: center;
}

.timer-btn {
  padding: 10px 20px;
  margin: 5px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.timer-btn:hover {
  background-color: #0056b3;
}